.input-text {
    outline: none;
    border: none;
    font-size: 1.2em;
    border-bottom: 2px solid gray;
    margin-top: 10px;
    margin-bottom: 10px;
    padding: 0.2em;
    color: indigo;
    -webkit-transition: all 300ms ease-in-out;
    -o-transition: all 300ms ease-in-out;
    transition: all 300ms ease-in-out;
}

.input-text:focus {
    border-bottom: 2px solid indigo;
}


