.add-button {
    border: none;
    width: 5em;
    height: 5em;
    border-radius: 50%;
    background: indigo;
    color: white;
    cursor: pointer;
    -webkit-transition: all 100ms ease-in-out;
    -o-transition: all 100ms ease-in-out;
    transition: all 100ms ease-in-out;
    display: block;
    bottom: 30px;
    right: 30px;
    position: absolute;
    outline: none;
}

.add-button:hover {
    background: rgb(105, 2, 179);
}


